.interaction {
  display: flex;
  overflow: hidden;
}
.interaction .activityName {
  display: inline-block;
  width: 100%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  vertical-align: middle;
}
.interaction .interaction-right {
  min-width: 300px;
  background: #222636;
  box-shadow: -1px 0px 10px 0px rgba(0, 0, 0, 0.4);
  position: relative;
  box-sizing: border-box;
  overflow: hidden;
  position: "relative";
  transition: all 0.2s linear;
}
.interaction .interaction-right h2 {
  text-align: center;
  font-size: 16px;
  color: rgba(255, 255, 255, 0.8);
  line-height: 50px;
  margin: 0;
  box-shadow: 0px 1px 10px 0px rgba(0, 0, 0, 0.4);
}
.interaction .interaction-right .interaction-right-bottom {
  display: flex;
  align-items: center;
  height: 173px;
  width: 100%;
  color: rgba(255, 255, 255, 0.8);
  border-top: solid 1px #000000;
  position: absolute;
  bottom: 0;
  background: #222736;
}
.interaction .interaction-right .interaction-right-bottom > div {
  width: 100%;
  min-height: 127px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
.interaction .interaction-right .interaction-right-bottom .interaction-right-bottom-title {
  font-size: 16px;
  padding-left: 10px;
  margin: 10px 0 10px 10px;
  line-height: 16px;
}
.interaction .interaction-right .interaction-right-bottom .ant-col {
  text-align: center;
  font-size: 14px;
  margin-top: 20px;
  cursor: pointer;
}
.interaction .interaction-right .interaction-right-bottom .ant-col p {
  color: #FFFFFF;
  margin-top: 8px;
  font-size: 12px;
}
.interaction .interaction-right .interaction-right-content::-webkit-scrollbar {
  width: 1px;
}
.interaction .interaction-right .interaction-right-content {
  overflow: auto;
  max-height: calc(100vh - 223px);
}
.interaction .interaction-right .interaction-right-content::-webkit-scrollbar {
  display: none;
}
.interaction .interaction-right .interaction-right-content img {
  width: 45px;
  height: 45px;
  border-radius: 50%;
}
.interaction .interaction-right .interaction-right-content .ant-row {
  margin: 7px 0;
  padding: 7px 15px;
  color: rgba(255, 255, 255, 0.8);
  transition: all 0.2s linear;
  padding-left: 0;
  padding: 0;
}
.interaction .interaction-right .interaction-right-content .ant-row .ant-btn {
  width: 50px;
  height: 30px;
  padding: 0;
  font-size: 12px;
}
.interaction .interaction-right .interaction-right-content .ant-row .ant-tag {
  font-size: 10px;
  margin-left: 10px;
}
.interaction .interaction-right .interaction-right-content .ant-row .ant-tag img {
  width: 10px;
  height: 10px;
  margin-right: 3px;
  vertical-align: -1px;
}
.interaction .interaction-right .interaction-right-content p {
  margin-bottom: 5px;
  font-family: PingFangSC-Regular, PingFang SC;
}
.interaction .interaction-right .interaction-right-content p span {
  margin-right: 3px;
}
.interaction .interaction-right .interaction-right-content .ant-btn:hover {
  background: #fff;
  border: none;
}
.interaction .interaction-right .interaction-right-content .ant-btn {
  margin-top: 10px;
  background: none;
  color: rgba(58, 137, 253, 0.7);
  border: 1px solid rgba(58, 137, 253, 0.7);
}
.interaction .interaction-right .interaction-main {
  width: 100%;
  position: relative;
  height: 100vh;
}
.interaction .close {
  position: absolute;
  left: 30px;
  color: #fff;
  transition: all 0.2s linear;
  font-size: 12px;
  cursor: pointer;
}
.screen-left-bottom {
  width: 100%;
  height: 50px;
  background: #222736;
  box-shadow: 0px 1px 2px 0px #3a89fd;
  position: absolute;
  bottom: 0px;
}
.screen-left-bottom p {
  color: #fff;
  font-size: 16px;
  display: inline-block;
  width: 120px;
  line-height: 50px;
  text-align: center;
  cursor: pointer;
  transition: all 0.2s linear;
  height: 48px;
}
.screen-left-bottom p:hover {
  background: #302e45;
  color: #3a89fd;
}
.screen-left-bottom .switch {
  width: 60px;
  line-height: 50px;
  text-align: center;
  cursor: pointer;
  transform: tra;
  transition: all 0.2s linear;
  color: #fff;
  font-size: 16px;
  float: right;
  background: #3a89fd;
}
.statusImg {
  width: 55px !important;
  height: 18px !important;
  border-radius: 0 !important;
  vertical-align: -3px;
}
.random-spin {
  color: #fff;
  margin-top: 25vh;
}
.random-spin .ant-spin-dot-item {
  background: #fff;
}
.random-spin .ant-spin-text {
  margin-top: 15px;
  font-size: 16px;
}
.random-modal {
  width: 100%;
  text-align: center;
  margin-top: 12vh;
}
.random-modal img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
}
.random-modal p {
  color: #fff;
  margin: 15px;
}
.random-modal .ant-btn {
  width: 200px;
  height: 38px;
  background: #27cda2;
  border-radius: 22px;
  color: #fff;
}
.contranst-img {
  width: 90% !important;
  margin: 0 auto;
}
.contranst-img > div {
  width: 100%;
  height: 700px;
}
.contranst-img > div .ant-modal-body {
  height: 600px;
  max-height: 600px;
}
.contranst-img > div .ant-modal-body .contranst-img-box {
  width: 100%;
  height: 100%;
  display: flex;
  overflow: hidden;
}
.contranst-img > div .ant-modal-body .contranst-img-box img {
  flex: 1;
  margin: 0 2%;
}
.contranst-img .ant-modal-footer {
  display: none;
}
.screen-confirm .ant-modal-confirm-btns .ant-btn:nth-child(1) {
  display: none;
}
.interaction .interaction-right .interaction-right-content .ant-row.activityItem {
  padding: 0;
  box-sizing: border-box;
}
.interaction .interaction-right .interaction-right-content .ant-row.activityItem > p {
  color: white;
  font-size: 16px;
  color: #FFFFFF;
  margin-top: 20px;
  margin-bottom: 15px;
  margin-left: 20px;
}
.contranst-video {
  height: 60%;
  margin: 0 auto;
}
.contranst-video > div,
.contranst-video .ant-modal-content,
.contranst-video .ant-modal-body {
  width: 100%;
  height: 100%;
}
.contranst-video video {
  width: 100%;
  height: 90%;
  margin: 0 auto;
}
.contranst-video .ant-modal-footer {
  display: none;
}
.right_icon {
  display: flex;
  align-items: center;
  line-height: 45px;
}
.img_comparison {
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0);
  z-index: 2000;
}
.img_comparison .img_box {
  width: 100%;
  height: 100%;
  padding: 0px 105px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.img_comparison .img_box img {
  margin: 0px 15px;
  height: 77%;
}
.right-control {
  color: #ffffff;
}
.right-control > div.ant-col > h2 {
  color: #ffffff;
  font-weight: normal;
  font-size: 16px;
}
.right-control .interaction-right-content .activityItem > p {
  margin-left: 20px;
}
.right-control .interaction-right-content .activityItem .item-item:hover {
  background: rgba(255, 255, 255, 0.1);
}
.right-control .interaction-right-content .activityItem .item-item {
  cursor: pointer;
  box-sizing: border-box;
  height: 70px;
  margin: 7px 0px;
  display: flex;
  align-items: center;
}
.right-control .interaction-right-content .activityItem .item-item .ant-col-5 {
  padding-left: 18px;
  margin-right: 10px;
}
.right-control .interaction-right-content .activityItem .item-item p {
  color: rgba(255, 255, 255, 0.8);
  font-size: 12px;
}
.right-control .interaction-right-content .activityItem .item-item p span.activityName {
  color: #ffffff;
  font-size: 14px;
}
.right-control .interaction-right-content .activityItem .item-item .close svg {
  color: #ffffff;
}
.img-dialog {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 50%;
  left: 18px;
  width: 45px;
  height: 45px;
}
.active_bg {
  background: rgba(255, 255, 255, 0.1);
}
.right_empty .ant-empty-description p {
  font-size: 14px;
  color: white;
  margin: 0;
}
.right_empty .ant-empty-image img {
  width: 80px !important;
  height: 82px !important;
}
.empty_y {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.empty_y p {
  font-size: 14px;
}
#previewVideo {
  outline: none;
}
